<template>
    <div v-if="show">
        <Head :title="$route.query.name" :show="true"></Head>
        <div>
            <img :src="detail.img"  alt="" style="width:100%;height:300px">
            <h2>{{detail.name}}</h2>
            <h2>折扣 {{detail.discount}}</h2> 
            <h2>价格 {{detail.price}}</h2>
            <h2>评分 <van-rate v-model="rating" /> </h2>
            <p>数量 <van-stepper v-model="count" /></p>   
        </div>
        <van-button type="danger" >  商品规格 </van-button>
        <!-- <van-sku
            v-model="flag"
            :sku="sku"
            :goods="goods"
            :goods-id="goodsId"
            :quota="quota"
            :quota-used="quotaUsed"
            :hide-stock="sku.hide_stock"
            :message-config="messageConfig"
            @buy-clicked="onBuyClicked"
            @add-cart="onAddCartClicked"
        /> -->
        <van-goods-action>
            <van-goods-action-icon
                icon="chat-o"
                text="客服"
                @click="callOne"
            />
            <van-goods-action-icon
                icon="cart-o"
                :info="total"
                text="购物车"
                @click="gotoCart"
            />
            <van-goods-action-button
                type="warning"
                text="加入购物车"
                @click="addToshopcar"
            />
            <van-goods-action-button
                type="danger"
                text="立即购买"
                @click="paynow"
            />
        </van-goods-action>
    </div>
</template>

<script>
import {getUsername} from "@/utils";
export default {
    data(){
        return {
            show:false,
            detail:{},
            flag:false,
            rating:5,
            count:1,
            total:0
        }
    },
    methods:{
        callOne(){

        },
        gotoCart(){

        },
        addToshopcar(){
            this.$axios.post("/vue/addShopCar",{
                username:getUsername(),
                goodId:this.$route.params.goodId,
                count:this.count,
                goodInfo:this.detail,
                
            }).then(res=>{
                this.total += this.count;
            })  
        },
        paynow(){

        }

    },
    mounted(){
        console.log(this.$route.params.goodId);

        this.$axios.get("/vue/getGoodDetail",{
            params:{
                goodId:this.$route.params.goodId
            }
        }).then(res=>{
            this.detail = res.data.result;
            this.show = true;
        });



        this.$axios.post("/vue/getShopcarInfo",{
            username:getUsername(),
        }).then(res=>{
            this.total = res.data.total;
        })
    }
}
</script>
